.switchButton {
  display: flex;
  align-items: center;
  gap: 15px;
}
.switchButton .switchButton__prefix {
  color: var(--disabled);
  transition: all 0.2s ease-in-out 0s;
}
.switchButton .switchButton__switcher {
  position: relative;
  width: 40px;
  height: 22px;
  cursor: pointer;
  border-radius: 14px;
  background-color: var(--disabled-light);
  transition: all 0.2s ease-in-out 0s;
}
.switchButton .switchButton__switcher::before {
  position: absolute;
  content: "";
  top: 3px;
  left: 3px;
  width: 17px;
  height: 17px;
  background-color: var(--disabled);
  border-radius: 50%;
  transition: all 0.2s ease-in-out 0s;
}
.switchButton .switchButton__switcher .switchButton__input {
  width: 0;
  height: 0;
  opacity: 0;
}
.switchButton.isActive .switchButton__prefix {
  color: var(--beige-2);
}
.switchButton.isActive .switchButton__switcher {
  background-color: var(--beige-1);
}
.switchButton.isActive .switchButton__switcher::before {
  left: 20px;
  background-color: var(--beige-2);
}